:root {
 font-size: 1rem;
--BGColor: "#C5DB94";
--TitleColor: "#443427";
--SubtitleColor: "#68894D";
--TextColor: "#4d453f";
--LinkColor: var(--SubtitleColor);
--ALinkColor: "#f2ffd6";
--VLinkColor: var(--TitleColor);
--ShadowColor: "#2e251e80";
}

body::before {
  content: "";
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background-color: #C5DB94;
  background: linear-gradient(90deg, #C5DB94ff 60%, #68894Daa 100%);  
  background-image:  url(/assets/TurtlePattern.png);
  background-size: 6.66rem;
  filter: contrast(20%) blur(2px) brightness(120%) sepia(90%) hue-rotate(40deg) saturate(150%);
  z-index: -1;
}

body {
  margin: 0;
  color: #4d453f;
  font-size: 1.15rem;
  font-weight: bold;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  text-decoration: none;
  color: #68894D;
}
a:visited {
  color:#443427;
}
a:active {
  color: #f2ffd6;
}
a:hover {
  color: #f2ffd6;
}
h1 {
  color: #443427;
}
h2, h3 {
  color: #68894D;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}