.App-header {
  background-color: #C5DB94;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #4d453f;
  
}

.App-link {
  color: #68894D;
}

h1, h2, h3 {font-family: lemon, cursive;}

p {font-family: delius, sans-serif;}

@media only screen and (min-width: 600px) {
  :root{
    font-size: 1.33rem;
  }
}
@media only screen and (max-width: 600px) {
  iframe, .Modal img{
    width: 98vw;
    height: auto;
  }

  .callToActionHeader{
      max-width: 92vw;
  }
  .callToActionHeader iframe {
    max-width: 90vw;
  }
  .Callout main{
    flex-flow: column;
  }
  .Description{
    max-width: 240px;
    padding: 0;
  }
  .sketchfab-embed-wrapper {
    max-width: 98vw;
  }
  .Images{
    width: 240px;
    margin: auto;
  }
  .Description{
    max-width: 260px;
    margin: auto;
  }
}